import {Injectable} from '@angular/core';
import {SnotifyService} from "ng-snotify";


@Injectable({
  providedIn: 'root'
})
export class ContractHandlerService {
  availableErrors: any = {
    "0": "Отказано в доступе",
    "1": "Имя пользователя не должно быть меньше 3 символов или превышать 15 символов",
    "2": "Такое имя уже занято другим пользователем, пожалуйста, укажите другой никнейм.",
    "3": "Неверная цена",
    "4": "Дом не существует",
    "5": "Только новый пользователь может приобрести дом",
    "6": "Вы должны быть зарегистрированы",
    "7": "Стать супер инвестором сейчас нельзя",
    "8": "Улучшение такого уровня не существует",
    "9": "Вы не можете получить бонус так как время еще не пришло",
    "10": "Не хватает ресурсов",
    "11": "Улучшение на текущий уровень временно недоступно",
    "12": "Предмет не существует",
    "13": "Сообщение не должно превышать 100 символов!",
    "14": "Пользователь уже является модератором",
    "15": "Причина бана не должна быть меньше 10 символов или превышать 100 символов",
    "16": "Вы не можете отправлять сообщения, так как у вас бан чата",
    "17": "Сообщение уже удалено",
    "18": "Этот сундук не продается!",
    "19": "Вы не можете открыть этот сундук!",
    "20": "Повторите попытку позже, нельзя выполнять данную операцию так часто.",
    "21": "У вас недостаточно ресурсов",
    "22": "Для улучшения предмета Ваш уровень должен быть равен 30 или быть больше. Для повышения уровня используйте подземелья",
    "23": "Вы не можете улучшить этот предмет, так как он уже зачарован.",
    "24": "Недостаточно ресурсов.",
    "25": "Уровень дома не позволяет выполнить это действие",
    "26": "Вы не можете поставить предмет на аукцион, так как он уже зарегистрирован!",
    "27": "Вы не можете получить дивиденды, так как время еще не пришло.",
    "1000": "Эта вещь не пренадлежит вам!",
  };


  constructor(private notify: SnotifyService) {

  }


  errorHandle(error) {
    let message = error.output ? this.dataMessageConvert(error.output.contractResult[0]) : typeof error === "string" ? error : error.message;
    let bodyDescription = this.availableErrors.hasOwnProperty(message) ? this.availableErrors[message] : message;

    this.notify.error(bodyDescription, {
      position: 'rightTop'
    });


  }

  dataMessageConvert(message) {
    let string = window['tronWeb'].toUtf8(message);
    return string.replace(/\D/g, '');
  }
}
