import {Injectable} from '@angular/core';
import {HttpClient} from "@angular/common/http";
import {StoreService} from "./store/store.service";

@Injectable({
  providedIn: 'root'
})
export class LangService {
  loadingComponentDictionary: any = {};
  langIdxByLang: any = {};
  userLang: any = localStorage.getItem('lang') || 'en';

  constructor(private http: HttpClient, private store: StoreService) {
    this.langIdxByLang = {'ru': 0, 'en': 1, 'china': 2};

    this.loadingComponentDictionary = {
      loading: this.getObjectByLang('Загрузка, пожалуйста подождите', 'Loading please wait', '下载请稍候。'),
      tronLink: this.getObjectByLang('Расширение TronLink не установлено, для того чтобы начать игру, необходимо установить расширение.', 'TronLink extension is not installed, in order to start the game, you must install the extension.', '未安装TronLink扩展程序，要启动游戏，必须安装扩展程序。'),
      needAuth: this.getObjectByLang('Необходимо авторизоваться в расширении TronLink', 'You must log in to the TronLink extension', '您必须登录到TronLink扩展'),
      tronLinkTest: this.getObjectByLang('Проверяю, установлено ли у Вас расширение TronLink.', 'Checking if you have the TronLink extension installed.', '检查您是否安装了TronLink扩展。'),
      imageLoading: this.getObjectByLang('Загружаю анимацию и изображения', 'Loading animations and images', '加载动画和图像。'),
      loadLibrary: this.getObjectByLang('Загрузка скриптов и библиотек', 'Download scripts and libraries', '下载脚本和库'),
      loadReference: this.getObjectByLang('Загружаю справочник', 'Loading reference', '加载参考'),
      auth: this.getObjectByLang('Авторизация в TronLink', 'Log in to TronLink', '登录到TronLink'),
      loadLocation: this.getObjectByLang('Загрузка локации..', 'Loading location..', '正在加载位置..'),
      connectToSmartContract: this.getObjectByLang('Подключение к смартконтракту', 'Connect to a smart contract', '连接到智能合约'),
      loadDataOfUser: this.getObjectByLang('Загрузка данных пользователя', 'Loading user data', '加载用户数据'),
      loadDataFromStore: this.getObjectByLang('Загружаю данные из магазина', 'Loading data from the store', '从商店加载数据'),
      loadDataOfHome: this.getObjectByLang('Загрузка данных вашего дома', 'Loading data of your home', '正在为您的房屋加载数据'),
      loadMainProjectInfo: this.getObjectByLang('Загрузка основной информации по проекту', 'Loading game information', '载入游戏信息'),
      loadDataOfLatestOperations: this.getObjectByLang('Загружаю данные о последних операциях', 'Loading data on recent transactions', '加载最近交易的数据'),
      loadInventory: this.getObjectByLang('Загрузка инвентаря', 'Load inventory items', '加载库存项目'),
      chestItemLoad: this.getObjectByLang('Загрузка основной информации по сундукам', 'Load main information of chests', '加载箱子的主要信息'),
      loadHouseInfo: this.getObjectByLang('Загружаю информацию по дому', 'Load home information', '加载家庭信息'),
      loadPartnerInfo: this.getObjectByLang('Загрузка информации по партнерской программе', 'Load partner information', '加载合作伙伴信息'),
      loadItemsFromChest: this.getObjectByLang('Загрузка предметов из сундука', 'Loading items from chest', '从箱子里装东西'),
      loadUserItems: this.getObjectByLang('Загрузка предметов пользователя', 'Loading user items', '加载用户项目'),
      loadAllItems: this.getObjectByLang('Загрузка игровых предметов', 'Loading game items', '载入游戏物品'),
      loadAdventureInfo: this.getObjectByLang('Загрузка информации о последних посещениях', 'Loading adventure info', '加载冒险信息'),
      loadTradeItems: this.getObjectByLang('Загрузка предметов с аукциона', 'Load auction items', '加载拍卖品'),
    }
    // this.lang.loadingComponentDictionary['key'][this.lang.getCurrentLang()]
  }


  changeLang(lang) {
    this.userLang = lang;
    localStorage.setItem('lang', lang);
  }

  getCurrentLang() {
    return this.userLang;
  }


  getAvailableLanguages() {
    return {
      'ru': {name: 'Русский', className: 'flag-ru', key: 'ru'},
      'en': {name: 'English', className: 'flag-us', key: 'en'},
      'china': {name: '中国', className: 'flag-china', key: 'china'}
    }
  }

  loadDictionary() {
    return new Promise(resolve => {
      if (Object.values(this.store.dictionary).length) {
        resolve();
        return false;
      }
      this.http.get('assets/dictionary.json').subscribe(response => {
        this.store.dictionary = response;
        resolve();
      });
    });
  }

  translate(key) {
    return this.store.dictionary[key] ? this.store.dictionary[key][this.langIdxByLang[this.userLang]] : '-----------';
  }

  getObjectByLang(ru, en, china) {
    return {
      ru,
      en,
      china
    }
  }
}
